.event-block {
	padding-top: 150px;
	@include md {
		padding-top: 60px; }
	&__inner {
		@include ms {
			padding: 0; } }
	&__head {
		display: flex;
		align-items: center;
		flex-wrap: wrap; }
	&__title {
		max-width: 33%;
		@include md {
			max-width: none;
			width: 100%; }
		@include ms {
			padding: 0.67em 16px; } }
	&__list {
		padding-top: 60px;
		li {
			@include sm {
				&:not(:first-child) {
					margin-top: 30px; } } }
		@include sm {
			max-width: 400px;
			margin: 0 auto;
			padding-top: 13px;
			// &.row
 } }			// 	margin: 0
	&__tabs {
		margin-top: 30px;
		margin-left: auto;
		@include md {
			margin: 0; }
		@include sm {
			display: none; } }
	&__btn {
		margin-top: 30px;
		@include lg {
			display: none; }
		&_mob {
			display: none;
			.btn {
				min-width: 288px; }
			@include lg {
				display: block;
				text-align: center;
				margin-top: 40px; } } } }
