.course-block {
	padding-top: 155px;
	@include md {
		padding-top: 80px; }
	&.pilates-block {
		&:not(:first-child) {
			padding-top: 125px; } }
	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__list {
		padding-top: 65px;
		@include md {
			padding-top: 20px;
			max-width: 610px;
			margin: 0 auto; }
		li {
			@include md {
				margin-top: 16px; } }
		.pilates-block & {
			padding-top: 45px; } }
	&__btn {
		@include sm {
			display: none; }
		&_mob {
			display: none;
			.btn {
				min-width: 288px; }
			@include sm {
				display: block;
				text-align: center;
				margin-top: 34px; } } } }
