.testimonial-block {
	padding-top: 150px;
	@include lg {
		padding-top: 78px; }
	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__list {
		padding-top: 70px;
		@include md {
			max-width: 400px;
			margin: 0 auto;
			padding-top: 40px; }
		li {
			@include md {
				&:not(:first-child) {
					margin-top: 16px; } } } }
	&__btn {
		@include sm {
			display: none; }
		&_mob {
			display: none;
			.btn {
				min-width: 288px; }
			@include sm {
				display: block;
				text-align: center;
				margin-top: 60px; } } } }
