// 												COURSE
.course {
	display: block;
	padding: 1.7em 2em;
	background-color: $white;
	box-shadow: 0 20px 56px -33px rgba($black, 0.2);
	min-height: 300px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	border-radius: 10px;
	transition: background-color .4s, color .4s, box-shadow .4s, transform .15s;
	&.course_img {
		padding: 30px; }
	@include hover {
		background-color: $orange;
		color: $white;
		box-shadow: 0 25px 42px -14px $orange;
		transform: translateY(-8px);
		.course__aside {
			color: $white; }
		.course__bottom {
			color: $white; }
		.course__price:before {
			background-color: $white; }
		.course__title.title {
			color: $white; } }
	@include lg {
		padding: 27px; }
	@include sm {
		min-height: 273px;
		padding-bottom: 17px; }
	&__img {
		border-radius: 10px;
		overflow: hidden;
		img {
			display: block;
			max-width: 100%; } }
	&__aside {
		color: $grayLight;
		transition: color .4s;
		.course_img & {
			margin-top: 15px; } }
	&__bottom {
		display: flex;
		padding-top: 1em;
		@include f(1.2em, 700);
		letter-spacing: normal;
		color: $black;
		transition: color .3s;
		@include lg {
			font-size: 16px; } }
	&__price {
		margin-left: 30px;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			@include circle(7);
			background-color: $black;
			left: -18.5px;
			top: 50%;
			margin-top: -3.5px;
			transition: background-color .4s;
			@include sm {
				@include circle(5);
				left: -13px;
				margin-top: -1.5px; } }
		@include sm {
			margin-left: 24px; } }
	&__title {
		margin-top: .4em;
		transition: color .4s; } }

// 												EVENT
.event {
	display: block;
	color: $black;
	transition: transform .15s;
	@include hover {
		transform: translateY(-8px); }
	&__img {
		img {
			max-width: 100%;
			display: block;
			@include sm {
				min-height: 300px; } } }
	&__date {
		font-size: 16px;
		line-height: 1.75;
		padding-top: 1.25em;
		letter-spacing: normal;
		span {
			text-transform: uppercase;
			color: $orange;
			font-weight: 700; }
		@include ms {
			padding: 20px 16px 7px; } }
	&__title {
		max-width: 300px;
		margin-top: 0.25em;
		&.title_md {
			@include md {
				font-size: 21px;
				line-height: 1.47; } }
		@include ms {
			padding: 0 16px;
			margin: 0; } } }

// 										INSTRUCTOR
.instructor {
	background-color: $white;
	box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
	border-radius: 10px;
	overflow: hidden;
	transition: transform .15s;
	@include hover {
		transform: translateY(-8px); }
	&__top {
		background-color: $blue;
		// padding-bottom: 34%
		height: 147px;
		position: relative;
		overflow: hidden;
		&:before {
			content: '';
			position: absolute;
			top: 1.33em;
			left: 2em;
			right: 2em;
			bottom: -1em;
			border: 1px solid $white;
			border-radius: 10px;
			@include md {
				top: 13px;
				left: 20px;
				right: 20px; } }
		@include ms {
			height: 98px; } }
	&__bottom {
		text-align: center;
		padding: 0 2em 2.66em;
		p {
			text-align: left;
			line-height: 1.9;
			@include sm {
				line-height: 1.75; } }
		@include md {
			padding: 0 20px 31px; } }
	&__img {
		@include circle(120);
		@include cover;
		// margin: -23% auto 0
		margin: -86px auto 0;
		overflow: hidden;
		z-index: 1;
		position: relative;
		@include ms {
			@include circle(81);
			margin-top: -58px; } }
	&__title {
		margin-top: 0.5em;
		@include md {
			&.title_md {
				font-size: 18px; } }
		@include ms {
			margin-top: 6px; } }
	&__aside {
		letter-spacing: 0.063em;
		color: $grayLight;
		margin-bottom: 1em;
		@include md {
			margin-bottom: 10px; } } }

// 									TESTIMONIAL
.testimonial {
	background-color: $white;
	padding: 2em 2em 2.66em;
	box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
	border-radius: 10px;
	transition: transform .15s;
	@include hover {
		transform: translateY(-8px); }
	&__top {
		display: flex;
		align-items: center; }
	&__img {
		flex-shrink: 0;
		@include circle(90);
		@include cover;
		overflow: hidden;
		margin-right: 1.33em; }
	&__aside {
		color: $grayLight;
		letter-spacing: 0.033em; }
	&__bottom {
		position: relative;
		margin-top: 1.6em;
		@include clr; }
	&__quote {
		width: 54px;
		height: 38px;
		margin-right: -27px;
		float: left;
		background-image: url(../img/quotes.svg);
		@include cover;
		top: 9px;
		left: 0;
		p {
			z-index: 1;
			position: relative;
			&:first-child {
				padding: 0 2em; } } } }

// 										FEEDBACK
.feedback {
	padding: 30px;
	background-color: $blue;
	@include sm {
		padding: 30px 16px; }
	&__wrap {
		padding: 150px 30px 0;
		@include md {
			padding: 80px 16px 0; }
		@include sm {
			padding: 80px 0 0; } }
	&__inner {
		border: 2px solid $white;
		padding: 2.36em 4em;
		@include lg {
			padding: 30px; }
		@include sm {
			padding: 26px 14px; } }
	&__title {
		text-align: center;
		margin-bottom: 1.28em;
		&.title {
			color: $white; }
		@include sm {
			text-align: left;
			margin-bottom: 28px; } }
	&__check {
		text-align: center;
		margin-top: 2.66em;
		color: $white;
		letter-spacing: normal;
		@include sm {
			margin-top: 22px; } } }

// 										FORM
.form {
	// width: 100%
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	&__btn {
		padding-left: 10px;
		width: 25%;
		@include md {
			width: 100%;
			margin-top: 30px;
			text-align: center;
			padding: 0; }
		@include sm {
			.btn {
				min-width: 0; } } } }
.form-group {
	// max-width: 300px
	width: 25%;
	padding: 0 10px;
	@include md {
		width: 100%;
		max-width: 500px;
		&:not(:first-child) {
			margin-top: 16px; } }
	@include sm {
		padding: 0; }
	input {
		color: $grayLight;
		@include verdana;
		@include f(15px, 400);
		letter-spacing: 0.066em;
		padding: 1.33em;
		background-color: $white;
		border-radius: 10px;
		width: 100%;
		&:focus {
			&::placeholder {
				font-size: 0; } }
		@include placeholder {
			color: $grayLight;
			@include verdana;
			@include f(15px, 400);
			letter-spacing: 0.066em; }
		@include sm {
			padding: 16px 20px; } } }
.checkbox {
	position: relative;
	cursor: pointer;
	padding-left: 40px;
	@include sm {
		font-size: 10px; }
	&__icon {
		position: absolute;
		top: -5px;
		left: 0;
		width: 25px;
		height: 25px;
		border-radius: 4px;
		border: 1px solid $white; }
	&__text {
		display: inline-block;
		border-bottom: 1px solid transparent;
		transition: border-color .3s;
		@include hover {
			border-color: currentColor; } }
	input {
		position: absolute;
		width: 1px;
		height: 1px;
		margin: -1px;
		padding: 0;
		border: 0;
		clip: rect(0 0 0 0);
		overflow: hidden;
		&:checked {
			~ .checkbox__icon {
				background-image: url(../img/check.svg);
				background-size: 70%;
				background-repeat: no-repeat;
				background-position: 50% 50%; } } } }

// 											SEMINAR
.seminar {
	color: $black;
	&__top {
		display: block;
		@include cover;
		border-radius: 10px;
		height: 0;
		padding-bottom: 57.75%;
		box-shadow: 0 20px 42px -24px rgba(0, 0, 0, 0.3);
		position: relative;
		transition: transform .15s;
		@include hover {
			transform: translateY(-8px); } }
	&__btn {
		position: absolute;
		left: 0;
		bottom: 0; }
	&__list {
		box-shadow: 0 20px 42px -18px rgba(0, 0, 0, 0.24);
		background-color: $white;
		border-radius: 10px;
		margin-top: 20px;
		overflow: hidden;
		@include sm {
			margin-top: 12px; } }
	&__item {
		font-weight: 700;
		letter-spacing: 0.033em;
		line-height: 1.66;
		&:not(:last-child) {
			border-bottom: 1px solid $grayBorder; }
		a {
			display: block;
			padding: 1.8em 2em;
			transition: background-color .3s, color .3s;
			@include hover {
				background-color: $orange;
				color: $white; }
			@include md {
				padding: 18px;
				font-size: 11px; } } } }
// 										MAP
.map {
	width: 100%;
	height: 533px;
	@include xs {
		height: 300px; } }
// 										CONTACTS
.contacts {
	color: $black;
	position: relative;
	transition: transform .15s;
	@include hover {
		transform: translateY(-8px); }
	&__info {
		position: absolute;
		bottom: -127px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 1;
		background-color: $white;
		@include md {
			position: static;
			transform: translateX(0);
			margin: 0 auto; } }
	.contacts-block & {
		@include md {
			margin-top: 40px; } } }
.contacts-info {
	max-width: 350px;
	width: 100%;
	padding: 2em;
	border-radius: 10px;
	box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
	@include xs {
		padding: 30px 15px; }
	.footer & {
		padding: 0;
		border-radius: 0;
		box-shadow: none;
		@include md {
			margin-top: 40px; } }
	&__title {
		&.title_md {
			@include md {
				font-size: 21px; } }
		.footer & {
			color: $white; } }
	&__aside {
		margin-top: 0.8em;
		margin-bottom: 0.1em;
		color: $grayLight;
		.footer & {
			color: $white;
			opacity: 0.5; } }
	// a
	// 	border-bottom: 1px solid transparent
	// 	transition: border-color .3s
	// 	+hover
	// 		border-color: currentColor
	address {
		font-style: normal;
		line-height: 1.73; } }
